.picture{
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  display: block;
  position: relative;
  border-radius: 4px;
}

.picture:hover::after{
  top: 5px;
  left: 5px;
  background-color: #f2eedf;
  border: 2px solid #f2eedf;
}

.picture::before{
  top: 0px;
  left: 0px;
  mix-blend-mode: screen;
  background-color: #0a192f;
}

.picture::after {
  content: "";
  top: 15px;
  left: 10px;
  display: block;
  position: absolute;
  width: 100%;
  max-width: 15rem;
  height: 100%;
  max-height: 20rem;
  border: 2px solid black;
  border-radius: 4px;
  transition: all 0.25s cubic-bezier(0.645,0.045,0.355,1);
  z-index: -1;
}

@media screen and (prefers-color-scheme: dark) {
  .picture::after {
    border: 2px solid #f2eedf;
  }
}

@media screen and (max-width: 765px) {
  .picture{
    top: 50px;
  }

  .picture img{
    height: 10rem;
  }

  .picture::after {
    height: 10rem;
    top: 10px;
    left: 9px;
  }
}

